@import url(https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@100;300;400;500;600;700;800;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

#root > div {
  display: flex;
  min-height: 100vh;
  flex-direction: column;
}
*,
::after,
::before{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

:root {
  --bg-primary: #FFFFFF;
  --bg-secondary: #FD5E5E;
  --txt-primary: #FF7171;
  --txt-secondary: #696767;
  --border-radius: 5px;
  --success: 	#42ba96;
  --background-success: rgba(66, 186, 150, 0.09);
  --background-danger: rgb(217, 83, 79, 0.09);
  --danger: #df4759;
  --info:  	#467fd0;
}  

body{
  font-family: 'Roboto Slab', serif;
  background-color: #FFFFFF;
  background-color: var(--bg-primary);
  /* position: relative; */
}


a {
  color: #FF7171;
  color: var(--txt-primary);
  text-decoration: none;
}

.container {
  width: 70%;
  margin: 0 auto;
}

.message {
  text-align: center;
  margin-top: 1rem;
  font-size: 10px;
  padding: 0.3rem 0;
  /* width: 30%; */
  margin-bottom: 0.5rem;
  color: #696767;
  color: var(--txt-secondary);
  font-weight: bold;
  
}

.success {
  border: 1.5px solid #42ba96;
  border: 1.5px solid var(--success);
  background-color: rgba(66, 186, 150, 0.09);
  background-color: var(--background-success);
  border-radius: 5px;
  border-radius: var(--border-radius);
}

.danger {
  border: 1.5px solid #df4759;
  border: 1.5px solid var(--danger);
  background-color: rgb(217, 83, 79, 0.09);
  background-color: var(--background-danger);
  border-radius: 5px;
  border-radius: var(--border-radius);
}

