:root {
  --main-color: #111;
  --loader-color: #4CAF50;
  --back-color: #A5D6A7;
  --time: 3s;
  --size: 3px;
}

.loader {
  overflow: hidden;
  height: 3px;
  height: var(--size);
  display: flex;
  align-items: center;
  align-content: center; 
  justify-content: flex-start;  
    z-index: 100000;
}

.loader__element {
    width: 100%;
    background: #A5D6A7;
    background: var(--back-color);

}

.loader__element:before {
    content: '';
    display: block;
    background-color: #4CAF50;
    background-color: var(--loader-color);
    height: 3px;
    height: var(--size);
    width: 0;
  -webkit-animation: getWidth 3s ease-in infinite;
          animation: getWidth 3s ease-in infinite;
  -webkit-animation: getWidth var(--time) ease-in infinite;
          animation: getWidth var(--time) ease-in infinite;
}

@-webkit-keyframes getWidth {
    100% { width: 100%; }
}

@keyframes getWidth {
    100% { width: 100%; }
}




