}


.platform {
    margin-top: 1rem;    
}

.platform .title {
    text-align: center;
    font-size: 1.3rem;
    color: #FF7171;
    color: var(--txt-primary);
    position: relative;

}

.platform .platform-container {
    display:flex;
    /* text-align: center; */
    width: 80%;
    justify-content: center;
    margin: 0 auto;
    margin-top: 3rem;
}

.platform .platform-container .platform-item{
    border: 1px solid #FF7171;
    border: 1px solid var(--txt-primary);
    border-radius: 6px;
    border-radius: var(--border-radius);
    padding: 1rem 2rem;
    display: flex;
    justify-content: center;
    margin-left: 1rem;
    cursor: pointer;
    width: 250px;
    color: #696767;
    color: var(--txt-secondary);
    font-size: 1.2rem;
    transition: 200ms ease-in;
    box-shadow: 10px 10px 20px rgba(0, 0, 0, 0.05);
}


.platform .platform-container .platform-item span {
    margin-top: 0.3rem;
    margin-left: 0.5rem;
}

.platform .platform-container .platform-item:hover {
    background-color: #FD5E5E;
    background-color: var(--bg-secondary);
    color: white;
    border-color: #FF7171;
    border-color: var(--txt-primary);
}

.platform .platform-container .platform-item:hover svg {
    fill: white;
}


@media screen and (max-width:570px) {
    .platform {
        margin-bottom: 3rem;
    }
    .platform .platform-container {
        display: block;
        width: 100%;
    }
    .platform .platform-container .platform-item {
        width: 90%;
        margin: 2rem auto;
    }
    .platform .title {
        text-align: center;
        font-size: 1rem;
    }
}
